@font-face{font-family:"font-1";src:url(../assets/fonts/JosefinSans-Medium.ttf);display:block}*{padding:0;margin:0;box-sizing:border-box}body{background:#f3f3f3;font-family:"font-1",sans-serif;line-height:1.5;letter-spacing:1px}html{scroll-behavior:smooth}li{list-style-type:none}h1{text-transform:uppercase;font-size:2rem}h2{text-transform:uppercase;font-size:1rem;color:#38849c;text-shadow:2px 2px 3px salmon}h3{font-size:.7rem;color:salmon;letter-spacing:1px;font-style:italic}h4{font-size:.7rem;color:salmon;letter-spacing:1px}.navigation ul{display:flex;justify-content:space-around;margin:30px}.navigation ul a{text-decoration:none;padding:10px 30px}.navigation ul a li{color:#61dafb;transition:.2s;cursor:pointer;text-transform:uppercase}.navigation ul a li:hover{color:#282c34}.navigation ul .nav-active{position:relative}.navigation ul .nav-active::after{content:"";height:4px;width:0;background:#61dafb;position:absolute;border-radius:10px;bottom:0px;left:50%;transform:translateX(-50%);-webkit-animation:anim .3s ease forwards;animation:anim .6s ease forwards}@-webkit-keyframes anim{to{width:100%}}@keyframes anim{to{width:100%}}.logo{display:flex;justify-content:center;align-items:center;margin:30px auto;width:150px;height:150px;border-radius:300px;padding:5px 0;box-shadow:4px 4px 10px rgba(15,64,56,.707);background:url(../../assets/img/main.jpg) center/cover;display:flex;flex-direction:column;border:6px double #282c34}.logo img{height:100px}.logo h3{color:#61dafb;font-size:.8rem}.countries{text-align:center}.countries .radio-container{justify-content:space-around;align-items:center;margin-bottom:20px;background:rgba(0,0,0,0);padding:10px;color:#61dafb}.countries .radio-container input{color:aqua;transform:translateY(1px)}.countries .radio-container label{margin-left:10px;cursor:pointer;transition:.2s}.countries .radio-container label:hover{color:salmon}.countries input[type=range]{-webkit-appearance:none;background:rgba(0,0,0,0);background-image:linear-gradient(to bottom, rgb(243, 243, 243), #61dafb, rgb(243, 243, 243));background-position:center;background-repeat:no-repeat;background-size:100% 20%}.countries input[type=range]::-webkit-slider-thumb{-webkit-appearance:none;width:2em;padding:1em;border-radius:50%;box-shadow:6px 6px 1em #f3f3f3 inset;background:linear-gradient(#61dafb, salmon) content-box,linear-gradient(-180deg, salmon, #61dafb) border-box}.countries input[type=radio]{-webkit-appearance:none;background:#cecccc;height:1em;width:1em;background-image:linear-gradient(to bottom, #61dafb, rgb(243, 243, 243), #61dafb, rgb(243, 243, 243), #61dafb);border:7px double #61dafb;border-radius:30px;box-shadow:1px 1px .5em salmon inset}.countries button{font-size:1.2rem;transition:.4s;background:#61dafb;border:none;color:#fff;margin:0px auto 10px;padding:10px 30px;border-radius:10px;cursor:pointer;transition:.2s}.countries button:hover{background:salmon;color:#282c34}.countries ul{display:flex;flex-wrap:wrap;justify-content:space-around;margin:20px;line-height:30px}.card{position:relative;height:200px;width:213px;border-radius:20px;transition:.3s;margin:2vh;background:#eef0f0;display:flex;align-items:center;justify-content:center;box-shadow:4px 4px 10px #282c34}.card:hover{transform:scale(1.1);z-index:2}.card:hover .infos{opacity:1;transform:scaleY(1);backdrop-filter:blur(5px);box-shadow:0px 0px 5px salmon}.card .infos{padding:2vh;position:absolute;top:0;left:0;background:rgba(227,225,225,.7);height:100%;width:100%;display:flex;flex-direction:column;justify-content:space-evenly;border-radius:10px;opacity:0;transform:scaleY(0);transition:.3s;text-align:center}.card img{height:125px;width:200px;object-fit:cover;border-radius:10px;box-shadow:0px 0px 100px #61dafb}.aboutUs{display:flex;flex-direction:column;margin:10vh;align-items:center;justify-content:center}.aboutUs p{font-size:1rem;padding-bottom:10vh;text-align:justify;font-size:1rem}@media screen and (min-width: 1280px){.aboutUs{display:grid;grid-template-rows:1fr;grid-template-columns:50% 50%;justify-content:space-around;align-items:start;height:100px;gap:45px}}h1{text-align:center;margin-top:100px}.article{margin:12vh auto;width:75%;background:#fff;box-shadow:0 2px 6px rgba(0,0,0,.1);border-radius:1vh;padding:10vh}.article .card-header{display:inline-block;vertical-align:middle;display:flex;width:100%;position:relative}.article .card-header h3{font-size:.7rem}.article .card-header h3::first-letter{font-size:1.5rem;color:#61dafb}.article .card-header em{font-size:.7rem;color:#38849c;margin:auto;height:100%;text-align:left;display:inline-block;vertical-align:middle}.article .card-header em::after{content:"★★★☆☆";position:absolute;width:10vh;height:10vh;margin-left:1vh;color:salmon;display:inline-block;vertical-align:middle}.article p{font-size:.9rem}.article textarea{width:100%;resize:none;height:70px;outline:none;padding:10px;text-align:justify;border-radius:5px;line-height:1.5;letter-spacing:1px}.article textarea:focus{box-shadow:none;border:none;outline:salmon dotted 3px;outline-offset:.05em;background-color:#61dafb}.article .btn-container{display:flex;justify-content:space-around;align-items:center;transition:.4s;margin:6vh 0 0}.article .btn-container button{width:100px;background:#61dafb;border:none;border-radius:3px;margin:0 auto;font-size:.8rem;width:20vh;height:5vh;font-size:.7rem;margin:0 1vh;cursor:pointer}.article .btn-container button:hover{filter:brightness(110%);background:#282c34;color:#fff}@media screen and (min-width: 1280px){.article .article .btn-container button{display:flex;justify-content:center;align-items:center;font-size:1rem;width:200px;transition:.4s}.article .article p{font-size:1.2rem}.article .article .card-header{display:flex;flex-direction:column-reverse}.article .article .card-header h3{font-size:2rem}.article .article .card-header h3::first-letter{font-size:2.5rem}.article .article .card-header em{font-size:1rem}}.blog-container h1{text-align:center;margin-bottom:10px}.blog-container form{display:grid;min-width:400px;text-align:center;width:75%;min-width:420px;margin:0 auto;outline:none}.blog-container form input,.blog-container form textarea{margin-bottom:5vh;padding:4vh;border-radius:5px;border:1px solid rgba(0,0,0,0);resize:none;box-shadow:0 2px 6px rgba(0,0,0,.14);transition:.3s;font-size:1rem}.blog-container form input:focus,.blog-container form textarea:focus{box-shadow:none;border:none;outline:#61dafb dotted 3px;outline-offset:.1em;background-color:salmon;color:#f3f3f3}.blog-container form textarea{height:30vh;outline:none}.blog-container form p{font-size:.8rem;color:red}.blog-container form input[type=submit]{width:200px;display:flex;align-items:center;justify-content:center;margin:0 auto;padding:3vh 8vh;font-size:1.2rem;cursor:pointer;transition:.4s;background:#61dafb;border:none;color:#f3f3f3;text-align:center}.blog-container form input[type=submit]:hover{background:salmon;color:#282c34}