@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/JosefinSans-Medium.ttf);
  display: block;
}

$font-1: "font-1", sans-serif;

$color-1: #61dafb;
$color-2: salmon;
$color-3: #282c34;
$color-4: #38849c;
$white: rgb(243, 243, 243);
$white2: rgb(206, 204, 204);

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: $white;
  font-family: $font-1;
  // @include verticalCenter;
  line-height: 1.5;
  letter-spacing: 1px;
}

html {
  scroll-behavior: smooth;
}

li {
  list-style-type: none;
}
h1 {
  text-transform: uppercase;
  font-size: 2rem;
}
h2 {
  text-transform: uppercase;
  font-size: 1rem;
  color: $color-4;
  text-shadow: 2px 2px 3px $color-2;
}

h3 {
  font-size: 0.7rem;
  color: $color-2;
  letter-spacing: 1px;
  font-style: italic;
}

h4{
  font-size: 0.7rem;
  color: $color-2;
  letter-spacing: 1px;

}