.card {
  position: relative;
  height: 200px;
  width: 213px;
  border-radius: 20px;
  transition: 0.3s;
  margin: 2vh;
  background: rgb(238, 240, 240);
  display: flex;
align-items: center;
justify-content: center;
box-shadow: 4px 4px 10px $color-3;

&:hover {
  transform: scale(1.1);
  z-index: 2;
}

&:hover .infos {
  opacity: 1;
  transform: scaleY(1);
  backdrop-filter: blur(5px);
  box-shadow: 0px 0px 5px $color-2;
}

.infos {
    padding: 2vh;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(227, 225, 225, 0.7);
    // color: $white;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    border-radius: 10px;
    opacity: 0;
    transform: scaleY(0);
    transition: 0.3s;
    text-align: center;
  }
  img {
    height: 125px;
    width: 200px;
    object-fit: cover;
    border-radius: 10px;
    box-shadow: 0px 0px 100px $color-1;
  }
}
