.logo {
  @include verticalCenter;
  margin: 30px auto;
  width: 150px;
  height: 150px;
  border-radius: 300px;
  padding: 5px 0;
  box-shadow: 4px 4px 10px rgba(15, 64, 56, 0.707);
  background: url(../../assets/img/main.jpg) center/cover;
  display: flex;
  flex-direction: column;
  border: 6px double $color-3;
  
  
  img {
    height: 100px;
    
  }
  h3 {
    // margin-left: 10px;
    color: $color-1;
    font-size: .8rem;
  }
}
