.countries {
  text-align: center;

  .radio-container {
    justify-content: space-around;
    align-items: center;
    margin-bottom: 20px;
    background: transparent;
    // border-radius: 20px;
    padding: 10px;
    color: $color-1;
    // box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.452);

    input {
      color: aqua;
      transform: translateY(1px);

      // &:focus {
      //   color: $color-1;
      // }
    }

    label {
      margin-left: 10px;
      cursor: pointer;
      transition: 0.2s;

      &:hover {
        color: $color-2;
      }
    }
  }
  /*==============================*/
  /* partie commune               */
  /*==============================*/
  input[type="range"] {
    -webkit-appearance: none;
    // background: $color-1;
    // height: 2em;
    background: transparent;
    background-image: linear-gradient(to bottom, $white, $color-1, $white);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 20%;
  }
  /*==============================*/
  /* le curseur                   */
  /*==============================*/
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 2em;
    padding: 1em; /* largeur du bord */
    // border: 1px solid $white;
    border-radius: 50%;
    // background: $color-1;
    box-shadow: 6px 6px 1em $white inset;
    background: linear-gradient($color-1, $color-2) content-box,
      /* le centre */ linear-gradient(-180deg, $color-2, $color-1) border-box; /* le bord */
  }
  input[type="radio"] {
    -webkit-appearance: none;
    background: $white2;
    height: 1em;
    width: 1em;
    // background: transparent;
    background-image: linear-gradient(
      to bottom,
      $color-1,
      $white,
      $color-1,
      $white,
      $color-1
    );
    // border: none;
    border: 7px double $color-1;
    border-radius: 30px;
    box-shadow: 1px 1px .5em $color-2 inset;
    // background-position: center;
    // background-repeat: no-repeat;
    // background-size: 100% 20%;
  }
