  button {
    font-size: 1.2rem;
    transition: 0.4s;
    background: $color-1;
    border: none;
    color: white;

    margin: 0px auto 10px;
    // background: none;
    // border: 2px solid $color-1;
    padding: 10px 30px;
    border-radius: 10px;
    cursor: pointer;
    transition: 0.2s;
    

