.blog-container {
  h1 {
    text-align: center;
    margin-bottom: 10px;
  }
  form {
    display: grid;
    min-width: 400px;
    text-align: center;
    width: 75%;
    min-width: 420px;
    margin: 0 auto;
    outline: none;

    input,
    textarea {
      margin-bottom: 5vh;
      padding: 4vh;
      border-radius: 5px;
      border: 1px solid transparent;
      resize: none;
      box-shadow: 0 2px 6px rgba(0, 0, 0, 0.14);
      transition: 0.3s;
      font-size: 1rem;

      &:focus {
        box-shadow: none;
        border: none;
        outline: $color-1 dotted 3px;
        outline-offset: 0.1em;
        background-color: $color-2;
        color: $white;
      }
    }
    textarea {
      height: 30vh;
      outline: none;
    }
    p {
      font-size: 0.8rem;
      color: red;
    }
    input[type="submit"] {
      width: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      padding: 3vh 8vh;
      font-size: 1.2rem;
      cursor: pointer;
      transition: 0.4s;
      background: $color-1;
      border: none;
      color: $white;
      text-align: center;

      &:hover {
        background: $color-2;
        color: $color-3;
      }
    }
  }
}
