.article {
  margin: 12vh auto;
  width: 75%;
  background: white;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  border-radius: 1vh;
  padding: 10vh;
  // display: flex;

  .card-header {
    display: inline-block;
    vertical-align: middle;
    display: flex;
    // justify-content: space-between;
    // align-items: center;
    // flex-direction: row-reverse;
    // margin: 5px 0 10px;
    // text-align: center;
    width: 100%;
    position: relative;

    h3 {
      font-size: 0.7rem;

      &::first-letter {
        font-size: 1.5rem;
        color: $color-1;
      }
    }
    em {
      font-size: 0.7rem;
      color: $color-4;
      margin: auto;
      height: 100%;
      text-align: left;
      display: inline-block;
      vertical-align: middle;

      &::after {
        content: "★★★☆☆";
        position: absolute;
        // z-index: 0;
        width: 10vh;
        height: 10vh;
        margin-left: 1vh;
        color: $color-2;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50% -50%);
        display: inline-block;
        vertical-align: middle;
        // flex-grow: 5;
        // border-radius: 100px;
        // border: 6px double aqua;
        // margin: 0 auto;
      }
    }
  }
  p {
    font-size: 0.9rem;
  }
  textarea {
    width: 100%;
    resize: none;
    height: 70px;
    outline: none;
    padding: 10px;
    text-align: justify;
    border-radius: 5px;
    line-height: 1.5;
    letter-spacing: 1px;

    &:focus {
      box-shadow: none;
      border: none;
      outline: $color-2 dotted 3px;
      outline-offset: 0.05em;
      background-color: $color-1;
    }
  }

  .btn-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    transition: 0.4s;
    margin: 6vh 0 0;

    button {
      width: 100px;
      background: $color-1;
      border: none;
      border-radius: 3px;
      margin: 0 auto;
      font-size: 0.8rem;
      width: 20vh;
      height: 5vh;
      font-size: 0.7rem;
      margin: 0 1vh;
      // margin-left: 20px;
      cursor: pointer;

      &:hover {
        filter: brightness(110%);
        background: $color-3;
        color: white;
      }
    }
  }
  @media screen and (min-width: 1280px) {
    .article {
      // display: grid;
      // grid-template-columns: 33% auto 33%;
      // grid-template-rows: auto;
      // width: 33%;
      .btn-container button {
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        width: 200px;
        transition: 0.4s;
      }
      p {
        font-size: 1.2rem;
      }

      .card-header {
        display: flex;
        flex-direction: column-reverse;
        h3 {
          font-size: 2rem;
          &::first-letter {
            font-size: 2.5rem;
          }
        }
        em {
          font-size: 1rem;
        }
      }
    }
  }
}
